.header__btn-sign-out {
  width: 56px;
  height: 22px;
  background-color: transparent;
  padding: 0;
  border: none;
  cursor: pointer;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.22;
  color: #a9a9a9;
  transition: opacity 0.6s;
}

.header__btn-sign-out:hover {
  opacity: 0.6;
}

@media screen and (max-width: 544px) {
  .header__btn-sign-out {
    display: none;
  }
}
